.navbarmain{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0;
    width: 100%;
    height: 60px;
    background: #29315a;
    color: #fff;
        border-bottom: solid 1px #fff;
       position: fixed;
       top: 0;
       z-index: 10000;
}

.navbcontent{
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;


}

.navlogo{
font-size: 25px;
user-select: none;
cursor: pointer;
}

.navlogo span{
    color: #ffc41d;
    
}

.navmenue
{
        display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    transition: all 0.2s ease-in-out;
    user-select: none;
}

.navmenue div{
    margin-right: 20px;
    cursor: pointer;
     padding: 10px;
     
}

.navdownload > button{
  background-color: #c33333;
   padding: 8px 15px;
    color: #fff;
    border-radius: 40px;
    outline: none;
    border: none;
    text-align: center;
    font-weight: 700;
    transition: all 0.4s ease-in-out;
    user-select: none;
}

button:hover{
    background-color: #ffc41d;
    color: black;
}

.navmenue div:hover{
 border-bottom: solid 3px #ffc41d;
   
}

.navhumbrger{
    display: none;
}

.pm{
    font-size: 50px;
}

.pm1{
    font-size: 20px;
}

.pm2{
    font-size: 18px;
}

.boxiconlang{
        background: #c33333;
    border-radius: 5px;
    /* opacity: 0.5; */
    margin: 10px;
}




/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 990px) {
.navmenue,.navdownload{
    display: none;
}




.navhumbrger{
    display: inline-block;
    cursor: pointer;
}

.pm{
    font-size: 25px;
}

.pm1{
    font-size: 18px;
}

.pm2{
    font-size: 15px;
}

}

@media only screen and (min-width: 700px) {
.navdownload{
    display: block;
}
.navbcontent {
    font-size: 15px;
}
}




@media only screen and (min-width: 991px) {


.container{
    max-width: 1200px;
}
.navbcontent {
    font-size: 15px;
}
}