.Hero{
     display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0;
    width: 100%;
    height: 100vh;
    background: #29315a;
    color: #fff;
    position: relative;
}

.herocontent{
   justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    text-align: center;
}



.imgbox{
    padding: 20px;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 990px) {
.Hero {
 
    flex-direction: column;
    margin-top: 50px;
     height: 100%;
}


}
