.word{
       background:#ffffff;
    padding: 10px;
    border-radius: 5px;
    word-break: break-word;
    line-height: 2;
    color: black;
    margin-top: 15px;
}

.bgp{
   background: #c33333;
    display: flex;
    justify-content: space-between;
    padding: 0px 10px;
    align-items: center;
    text-align: center;
    margin-top: 15px;
}

.word >li a {
    text-decoration: none;
    color: black;
    font-size: 20px;
    transition: all 0.3s ease-in-out;
    
}

.word >li:hover,.word >li a:hover {
   cursor: pointer;
    color: blue;
    font-weight: 700;
}





